import axios from 'axios'
import React, { useEffect, useState } from 'react'
import { useSearchParams } from 'react-router-dom'


export default function Details() {
    let[searchParams, setsearchParams]=useSearchParams()
    
    let currentid =searchParams.get('id')
    const [allmovies, setallmovies] = useState({})
    const [alltv, setalltv] = useState({})
    const [allpeople, setallpeople] = useState({})

    let imagebase= 'https://image.tmdb.org/t/p/original/'

    async function getDetails(media ,callback){
        let {data}= await axios.get(`https://api.themoviedb.org/3/${media}/${currentid}?api_key=58eb6469537b396d316caa0b7b9dd385`)
        callback(data)
    }
    useEffect(()=>{
        getDetails('movie',setallmovies)
        getDetails('tv',setalltv)
        getDetails('person',setallpeople)
      
        },[])
  return (
    <div>
       <>
       <div className="row">
       <div className="col-md-4">
       <img className='w-100' src={imagebase+allmovies.poster_path} alt="" />
        </div>
        <div className="col-md-8">
          <h2 className='text-danger'> {allmovies.title}</h2>
        <div><h5 className='text-white'>{allmovies.overview}</h5></div>
        <h4 className='text-info'>{allmovies.homepage}</h4>
        </div>
      </div>
       <div className="row">
       <div className="col-md-4">
       <img className='w-100' src={imagebase+alltv.poster_path} alt="" />
        </div>
        <div className="col-md-8">
          <h2 className='text-danger'> {alltv.name}</h2>
        <div><h5 className='text-white'>{alltv.last_air_date}</h5></div>
        <h4 className='text-info'>{alltv.number_of_seasons}</h4>
        </div>
      </div>
       <div className="row">
       <div className="col-md-4">
       <img className='w-100' src={imagebase+allpeople.profile_path} alt="" />
        </div>
        <div className="col-md-8">
          <h2 className='text-danger'> {allpeople.biography}</h2>
        <div><h5 className='text-white'>{allpeople.birthday}</h5></div>
        <h4 className='text-info'>{allpeople.also_known_as}</h4>
        </div>
      </div>
      

      
     
      </>

    </div>
  )
}
