import axios from 'axios'
import React, { Component } from 'react'
import reportWebVitals from './../../reportWebVitals';

export default class News extends Component {
    state={
        articles:[]
    }
    getNews= async ()=>{
        let {data}=  await axios.get('https://newsapi.org/v2/top-headlines?country=eg&business=sport&apiKey=3d84f9d6622644afad41b278773e83cd')
            this.setState({articles:data.articles})
           
    }
    componentDidMount(){
        this.getNews()
    }
  render() {
    return (
      <>
      {this.state.articles.length>0?<div className="row">
      {this.state.articles.map((artical,index)=> 
          <div key={index} className="col-md-4">
              <div className="items">
                  <h4> {artical.title.substr(0,10)}</h4>
                  <p>{artical.description}</p>
                  <img className='w-100' src={artical.urlToImage} alt="" />
                 
              </div>
          </div>
       )}
      </div>:<h2 className='text-center bg-danger'><i className='fas fa-spinner fa-spin fa-2xl'></i> </h2> }
      </>
    )
  }
}
