import React, { Component } from 'react'

export default class Child extends Component {
  render() {
    let{id,name,price,category,count,onsale}=this.props.productinfo
    console.log(this.props)
    return  <>
    <div className="col-md-4 mb-4 ">
      <div className="item bg-white text-dark position-relative p-5">
          <h2>name:{name}</h2>
          <h2>price:{price}</h2>
          <h2>category:{category}</h2>
          <h3>count: {count}</h3>
          {onsale? <div className='position-absolute bg-danger top-0 end-0 p-2 text-white '>onsale</div>:''}
            <button onClick={()=>this.props.delete(id)} className='bg-danger' >Delete</button>
            <button onClick={()=>this.props.update(this.props.productinfo)} className='bg-info' >Update</button>
      </div>
    </div>
      </>
    
  }
}
