import React, { Component } from 'react'
import Child from '../Child/Child'

export default class Parent extends Component {
    state={
        products:[
            {id:0, name:"toshiba",price:2000 ,category:"phone",count:5, onsale:true},
            {id:1, name:"oppo",price:6000 ,category:"phone",count:88,onsale:false},
            {id:2, name:"infinx",price:1000 ,category:"phone",count:20,onsale:true},
            {id:3, name:"samsung",price:8000 ,category:"phone",count:9,onsale:false},
            {id:4, name:"honor",price:2600 ,category:"phone",count:30,onsale:true},
            {id:5, name:"iphone",price:5000 ,category:"phone",count:8,onsale:true}
      ]         
    }
    deleteProduct=(id)=>{
   let products =[...this.state.products]
   products =products.filter((product)=>product.id!==id)
   this.setState({products})
    }
    updateProduct=(product)=>{
        let products =[...this.state.products]
       let  indexofitem=products.indexOf(product)
        products[indexofitem].count++
      this.setState(products)
    }

  render() {
    return (
  <div className="row bg-dark text-white p-2 ">  
    {this.state.products.map((product)=><Child 
    key={product.id}
    productinfo={product}
    delete={this.deleteProduct}
    update={this.updateProduct}
    />  )}

  </div>
    )
  }
  
}
