import jwtDecode from 'jwt-decode'
import React, { Component, useEffect, useState } from 'react'
import { Navigate, Route, Routes, useNavigate } from 'react-router-dom'
import About from './Componants/About/About'
import Home from './Componants/Home/Home'
import Login from './Componants/Login/Login'
import Mobileproject from './Componants/Mobileproject/Mobileproject'
import Navbar from './Componants/Navbar/Navbar'
import News from './Componants/News/News'
import NotFound from './Componants/Notfound/NotFound'
import Parent from './Componants/Parent/Parent'
import Reigester from './Componants/Reigester/Reigester'
import Webproject from './Componants/Webproject/Webproject'
import Details from './Details/Details'
import { MediaContextprovider } from './Componants/MediaContext/MediaContext';
import Tv from './Componants/Tv/Tv'
import Movie from './Componants/Movie/Movie'



export default function App() {
  const [userdata, setuserdata] = useState(null)
  let navigate =useNavigate()
  function getuserdata(){
    let tokendecode =jwtDecode(localStorage.getItem('usertoken'))
    setuserdata(tokendecode)
  }
useEffect(() => {

  
}, [userdata])
function logout(){
  localStorage.removeItem('usertoken')
setuserdata(null)
  navigate("/login")

}
useEffect(()=> {
  if(localStorage.getItem('usertoken')){
    getuserdata()
  }
},[])


function ProtectedRoute({children}){
  if(!localStorage.getItem('usertoken')){
    return <Navigate to= 'login'/>
  }else{
    return children;
  }
}

  return ( <>
   
      <Navbar userdata={userdata} logout={logout} />
      <div className='container'>
        <MediaContextprovider>
        <Routes>
      <Route path='/' element={  <Home/> }/>
      <Route path='home' element={  <ProtectedRoute> <Home/> </ProtectedRoute> }/>
      <Route path='login' element={ <Login getuserdata={getuserdata} />  }/>
      <Route path='reigester' element={  <Reigester/> }/>
      <Route path='news' element={ <ProtectedRoute>  < News/> </ProtectedRoute>  }/>
      <Route path='movie' element={ <ProtectedRoute>  < Movie/> </ProtectedRoute>  }/>
      <Route path='tv' element={ <ProtectedRoute>  < Tv/> </ProtectedRoute>  }/>
      <Route path='details' element={ <ProtectedRoute>  < Details/> </ProtectedRoute>  }/>
      <Route path='about' element= { <ProtectedRoute> <About/>  </ProtectedRoute> }>
      <Route path='web' element={<Webproject/>}/>
      <Route path='mobile' element={<Mobileproject/> }/>
        </Route>
      <Route path='parent' element={<ProtectedRoute><Parent/> </ProtectedRoute>}/>
      <Route path='*' element={<ProtectedRoute><NotFound/> </ProtectedRoute>}/>
        </Routes>

        </MediaContextprovider>
    
        </div>
       
    
    </>
  )
}
