import React, { Component } from 'react'
import { Link } from 'react-router-dom'



export default function Navbar(props) {
  return (
    <>
    <nav  className="navbar navbar-expand-lg navbar-dark bg-transparent">
  <div  className="container-fluid">
    <a  className="navbar-brand fa-bolder " href="#">Noxe</a>
    <button  className="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
      <span  className="navbar-toggler-icon"></span>
    </button>
    <div  className="collapse navbar-collapse" id="navbarSupportedContent">
    <ul  className="navbar-nav me-auto mb-2 mb-lg-0">
        <li  className="nav-item">
        </li>
     {props.userdata? <>
      <li  className="nav-item">
        <Link  className="nav-link active" aria-current="page" to={"home"}>Home</Link>
        </li>
      <li  className="nav-item">
        <Link  className="nav-link active" aria-current="page" to={"movie"}>Movie</Link>
        </li>
      <li  className="nav-item">
        <Link  className="nav-link active" aria-current="page" to={"tv"}>Tv</Link>
        </li>
        <li  className="nav-item">
        <Link  className="nav-link active" aria-current="page" to={"news"}>News</Link>
        </li>
        <li  className="nav-item">
          <Link  className="nav-link active" aria-current="page" to={"parent"}>Parent</Link>
        </li>
        <li  className="nav-item">
          <Link  className="nav-link active" aria-current="page" to={"about"}>About</Link>
        </li>
       
     </>:''}
      
      </ul>
      <ul  className="navbar-nav ms-auto mb-2 mb-lg-0">
      {props.userdata? <h5 className='me-2'>hello {props.userdata.first_name+ ' '+ props.userdata.last_name}</h5>:""}

        <li  className="nav-item">
        </li>
        <li  className="nav-item d-flex align-items-center me-3">
          <i className='fab fa-facebook-f mx-2'></i>
          <i className='fab fa-instagram mx-2'></i>
          <i className='fab fa-youtube mx-2'></i>
        </li>
{props.userdata?<>
  <li  className="nav-item">
          <span onClick={props.logout}  className="nav-link active" aria-current="page" to="logout">Logout</span>
        </li>
       
   </>  : <>
 <li  className="nav-item">
        <Link  className="nav-link active" aria-current="page" to={"reigester"}>Reigester</Link>
        </li>
        <li  className="nav-item">
        <Link  className="nav-link active" aria-current="page" to={"login"}>Login</Link>
        </li> </>}

      </ul>
  
    
    </div>
  </div>
</nav>
    </>
  )
}
