import React, { useContext } from 'react'
import { MediaContext } from './../MediaContext/MediaContext';
import style from './Home.module.css'
import { useNavigate } from 'react-router-dom';



export default function Tv() {
    let {alltv}=useContext(MediaContext)
    let imagebase= 'https://image.tmdb.org/t/p/original/'

    let navigate =useNavigate()
    function gotodetails(id){
      navigate({
        pathname:('/details'),
        search:(`?id=${id}`)
      })
  
    }
    
  return (
    <>
      <div className="row">
    <div className="col-md-4 d-flex align-items-center">
        <div className="item ">
          <div className={`w-25 my-2 ${style.brdr}`}></div>
        <h2>Trending</h2>
            <h2>tv</h2>
            <h2>to watch now</h2>
            <h2 className={style.secondcolor}>most watched tv by day</h2>
            <div className={style.brdr}></div>
        </div>
      </div>
      {alltv.map((tv,index)=> <div  onClick={()=>gotodetails(tv.id)} key={index} className="col-md-2">
        <div className="item">
        <img className='w-100' src={imagebase + tv.poster_path} alt="" />
                <h5>{tv.name.substr(0,10)}</h5>
        </div>
      </div> )}
    </div>
    
    
    </>
  )
}
