import React, { useContext } from 'react'
import { MediaContext } from './../MediaContext/MediaContext';
import style from './Home.module.css'
import { useNavigate } from 'react-router-dom';

export default function Movie() {

    let {allmovies}=useContext(MediaContext)
    let imagebase= 'https://image.tmdb.org/t/p/original/'

    let navigate =useNavigate()
    function gotodetails(id){
      navigate({
        pathname:('/details'),
        search:(`?id=${id}`)
      })
  
    }
  return (
    <div className="row">
    <div className="col-md-4 d-flex align-items-center">
      <div className="item ">
        <div className={`w-25 my-3 ${style.brdr}`}></div>
      <h2>Trending</h2>
          <h2>movies</h2>
          <h2>to watch now</h2>
          <h2 className={style.secondcolor}>most watched movies by day</h2>
          <div className={style.brdr}></div>
      </div>
    </div>
    {allmovies.map((movie,index)=> <div key={index} onClick={()=>gotodetails(movie.id)} className="col-md-2">
      <div className="item">
      <img className='w-100' src={imagebase + movie.poster_path} alt="" />
              <h5>{movie.title.substr(0,10)}</h5>
      </div>
    </div> )}
  </div>  )
}
