import axios from 'axios'
import Joi from 'joi'
import React, { useEffect, useState } from 'react'
import { useNavigate } from 'react-router-dom';
export default function Reigester() {
  const [error, seterror] = useState('')
  const [isloading, setisloading] = useState(false)
  const [errorlist, seterrorlist] =useState([])
  
  
  const [user, setuser] = useState({
  first_name:"",     
      last_name:"", 
      email:"",  
      password:"",  
       age:"" 
})
let navigate=useNavigate()
function gotologin(){
    let path ='/login'
    navigate(path)}

function getuser(e){
  let myuser = {...user}
  myuser[e.target.name]=e.target.value
setuser(myuser)
}

function valdiateform(user){
  let schema = Joi.object({
    first_name:Joi.string().required().min(2).max(10).alphanum(),
    last_name:Joi.string().required().min(2).max(10).alphanum(),
      age:Joi.number().required().min(10).max(80),
      email :Joi.string().email({ minDomainSegments: 2, tlds: { allow: ['com', 'net'] } }),
      password:Joi.string().pattern(/^[A-Z][a-z]{3,8}$/)


  })
  return schema.validate(user, {abortEarly:false})
}

async function submitRegister(e){
  e.preventDefault()

  setisloading(true)
  let validateresult =valdiateform(user)
  if(validateresult.error){
    seterrorlist(validateresult.error.details)
    console.log(validateresult)

  }else{
    let {data}= await axios.post("https://route-egypt-api.herokuapp.com/signup",user)
  if(data.message==='success'){
    gotologin();

    setisloading(false)

  }else{
    seterror(data.message)
    setisloading(false)

  }
  }
  
}


  return (
     <>
  <h2 className='py-3'>Registration form:</h2>
  {errorlist.map((error ,index)=>{
  if(index === 4){
    return <div key={index} className="alert alert-danger">password invalid</div>
  }else{
    return <div key={index} className="alert alert-danger">{error.message}</div>
  }
})}    
      {error?<div className="alert alert-danger">{error}</div>:''}
    
    <form onSubmit={submitRegister}>
 
      <label htmlFor="first_name">Firstname :</label>
      <input type="text" onChange={getuser} className='form-control' id='first_name' name='first_name' />

      <label htmlFor="last_name">lastname :</label>
      <input type="text" onChange={getuser}  className='form-control' id='last_name' name='last_name' />

      <label htmlFor="email">Email :</label>
      <input type="email" onChange={getuser}  className='form-control' id='email' name='email' />

      <label htmlFor="password">password :</label>
      <input type="password"onChange={getuser}  className='form-control' id='password' name='password' />

      <label htmlFor="age">age :</label>
      <input type="text" onChange={getuser}  className='form-control' id='age' name='age' />
       <button className='btn btn-info d-flex ms-auto mt-1'>
         {isloading? <i className='fas fa-spin fa-spinner'></i>:'Register'}
         </button> 
    </form>

    

    </>
  )
}
