import React, { createContext,useEffect ,useState } from 'react'
import axios from 'axios'

export let MediaContext=createContext()

export  function MediaContextprovider(props) {

  const [allmovies, setallmovies] = useState([])
  const [alltv, setalltv] = useState([])
  const [allpeople, setallpeople] = useState([])


  
 async function gettrendingitems(media,callback){
    let {data} = await axios.get(`https://api.themoviedb.org/3/trending/${media}/day?api_key=58eb6469537b396d316caa0b7b9dd385`)
    callback(data.results)
  }
  
  useEffect(() => {
    gettrendingitems('movie',setallmovies)
    gettrendingitems('tv',setalltv)
    gettrendingitems('person',setallpeople)
  
    
  }, [])
  return <MediaContext.Provider value={{allmovies, alltv, allpeople}}>
    {props.children}
  </MediaContext.Provider>



  // return (
  //   <div></div>
  // )
}
